export const getValidasiPOSelected = () => {
  var hasil = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
  return (dispatch) => {
    dispatch({
      type: GET_VALIDASI_PO_SELECTED,
      payload: {
        data: hasil,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
        error: true,
      },
    });
  };
};

